.footer {
  clear: both;
  border-top: 1px rgba(136, 136, 136, 0.85) solid;
  font-size: 20px; }
  .footer a {
    color: #1082A5;
    text-decoration: none;
    padding: 2px;
    -webkit-transition: all 0.5s ease-in;
    -o-transition: all 0.5s ease-in;
    transition: all 0.5s ease-in; }
    .footer a:hover {
      color: #1082A5;
      border-bottom: 1px #1082A5 solid; }

.heart {
  margin: 0 10px;
  width: 10px;
  height: 10px;
  background-color: #1082A5;
  position: relative;
  display: inline-block;
  -webkit-animation: pulse .5s infinite alternate ease-in;
          animation: pulse .5s infinite alternate ease-in; }

.heart:before {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #1082A5;
  position: absolute;
  top: 0;
  left: 50%;
  content: ""; }

.heart:after {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #1082A5;
  position: absolute;
  top: 50%;
  left: 0;
  content: ""; }

@-webkit-keyframes pulse {
  from {
    -webkit-transform: rotate(-135deg) scale(1);
            transform: rotate(-135deg) scale(1); }
  to {
    -webkit-transform: rotate(-135deg) scale(1.2);
            transform: rotate(-135deg) scale(1.2); } }

@keyframes pulse {
  from {
    -webkit-transform: rotate(-135deg) scale(1);
            transform: rotate(-135deg) scale(1); }
  to {
    -webkit-transform: rotate(-135deg) scale(1.2);
            transform: rotate(-135deg) scale(1.2); } }



